<template>
  <div class=" main_cont">
    <Header></Header>
    <Sidebar></Sidebar>
    <div class="content">
      <div class="form-card">
        <h2>Form Card</h2>
        <form @submit.prevent="create()">
          <label for="name">Name</label>
          <input type="text" id="name" v-model="name" name="name" required>

          <button  type="submit">Submit</button>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import Header from "../../components/Header";
import Sidebar from "../../components/Sidebar";
export default {
  components: {Sidebar, Header},
  data(){
    return{
      datas:[],
      name:''
    }
  },
  mounted() {
    this.$axiosDefault.get('category',
        {
          headers: {
            'Accept-Language': 'oz',
            'Authorization':  `Bearer  ${localStorage.getItem('_token')}`,
          }
        })
        .then(res=>{
          this.datas=res.data.data
          console.log(this.datas)
        })
  },
  methods:{
    create(){
      const form = new FormData();

      form.append("name", this.name);
      this.$axiosDefault.post('category/create',form,
          {
            headers: {
              'Accept-Language': 'oz',
              'Authorization':  `Bearer  ${localStorage.getItem('_toke')}`,
            }
          })
          .then(()=>{
            this.$toast("Ma'lumot kiritildi", {
              timeout: 2000,
              type:"success"
            });
          })
    }
  }
}
</script>
<style>
.form-card {
  margin-top: 100px;
  width: 100%;
  background-color: #f0f0f0;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.form-card h2 {
  text-align: center;
  margin-bottom: 20px;
}

.form-card form {
  margin-bottom: 20px;
}

.form-card label {
  font-weight: bold;
}

.form-card input {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  outline: none;
}

.form-card button {
  padding: 10px 15px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: 100%;
}

.form-card button:hover {
  background-color: #0056b3;
}
</style>
