<template>
  <div class="login-box">
    <h2>Login</h2>
    <form @submit.prevent="login()">
      <div class="user-box">
        <input type="email" v-model="email" name="" required="">
      </div>
      <div class="user-box">
        <input type="password" v-model="password" name="password" required="">
      </div>
      <button class="btn btn-primary">
        Submit
      </button>
    </form>
  </div>
</template>
<script>
export default {
  data(){
    return{
      email:'',
      password:''
    }
  },
  methods:{
    login(){
      this.$axiosDefault.post('login',{
        "email":this.email,
        "password":this.password
      })
      .then(res=>{
        console.log(res.data)
        localStorage.setItem('_toke',res.data.token)
        this.$router.push('/')
      })
      .catch(()=>{
        this.$toast("Login or password error", {
          timeout: 2000,
          type:"error"
        });
      })
    }
  }
}
</script>
