<template>
  <div class=" main_cont">
    <Header></Header>
    <Sidebar></Sidebar>
    <div class="content">
      <div class="create_btn">
        <router-link to="/catalog/create" class="btn btn-primary">Create</router-link>
      </div>
      <table class="admin-table">
        <thead>
        <tr>
          <th>Id</th>
          <th>Name</th>
          <th>Rasm</th>
          <th>Actions</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for="item in datas.data" :key="item">
          <td>{{item.id}}</td>
          <td>{{item.name}}</td>
          <td>{{item.name}}</td>
          <td>
            <button class="delete-button" @click="deleteGrop(item)">Delete</button>
          </td>
        </tr>
        <!-- Add more rows dynamically or manually as needed -->
        </tbody>
      </table>
      <div class="pagination-wrap">
        <b-pagination
            v-model="page"
            :total-rows="datas.total"
            :per-page="datas.per_page"
            first-number
            last-number
            aria-controls="my-table"></b-pagination>
      </div>
      Jami:{{datas.total}}
    </div>
  </div>
</template>
<script>
import Header from "../components/Header";
import Sidebar from "../components/Sidebar";
export default {
  components: {Sidebar, Header},
  data(){
    return{
      datas:[],
      page: 1,
      currentPage: 1,
    }
  },
  created() {
    this.page = this.currentPage
  },

  watch: {
    page(page) {

      this.$axiosDefault.get(`data?page=${page}`,
          {
            headers: {
              'Authorization':  `Bearer  ${localStorage.getItem('_toke')}`,
            }
          })
          .then(res=>{
            this.datas=res.data
            console.log(this.datas)
          })
    }
  },
  mounted() {
    this.$axiosDefault.get('data',
        {
          headers: {
            'Authorization':  `Bearer  ${localStorage.getItem('_toke')}`,
          }
        })
        .then(res=>{
          this.datas=res.data
          console.log(this.datas)
        })
  },
  methods:{
    deleteGrop(item){
      this.$axiosDefault.delete(`data/delete/${item.id}`,
          {
            headers: {
              'Accept-Language': 'oz',
              'Authorization':  `Bearer  ${localStorage.getItem('_toke')}`,
            }
          })
          .then(()=>{
            this.$axiosDefault.get(`data`,
                {
                  headers: {
                    'Accept-Language': 'oz',
                    'Authorization':  `Bearer  ${localStorage.getItem('_toke')}`,
                  }
                })
                .then(res=>{
                  this.datas=res.data
                })
          })
    }
  }
}
</script>
