<template>
  <div class=" main_cont">
    <Header></Header>
    <Sidebar></Sidebar>
    <div class="content">
      <div class="form-card">
        <h2>Form Card</h2>
        <form @submit.prevent="create()">
            <div class="row">
              <div class="col-xl-8">
                <label for="name">Nomi</label>
                <input type="text" id="name" v-model="name" name="name" required>
                <label for="name">Qabul qilingan sana
                  Arxeologiya ashyosi raqami
                </label>
                <input type="text" id="name" v-model="address" name="name" required>
                <label for="name">Tuzilgan sana </label>
                <input type="text" id="name" v-model="period" name="name" required>

              </div>
              <div class="col-xl-4">
                <label for="name">Ashyo turi</label>
                <div>
                  <select class="form-control" v-model="categorys">
                    <option :value="item.id" v-for="item in datas" :key="item">{{item.name}}</option>
                  </select>
                </div>
                <div class="create_form mt-4">
                  <label>Rasmi</label>
                  <input type="file" class="form-control"
                         id="cr_image"
                         @change="trySubmitFile"
                         accept="*"
                         name="datafiles"
                         ref="datafiles"
                  >
                </div>
              </div>
            </div>
          <button  type="submit">Submit</button>
        </form>
      </div>
    </div>
  </div>
</template>
<script>
import Header from "../../components/Header";
import Sidebar from "../../components/Sidebar";
export default {
  components: {Sidebar, Header},
  data(){
    return{
      datas:[],
      name:'',
      categorys:'',
      imageUrl:[],
      myImages:[],
      files_name:[],
      address:'',
      period:''
    }
  },
  mounted() {
    this.$axiosDefault.get('category',
        {
          headers: {
            'Accept-Language': 'oz',
            'Authorization':  `Bearer  ${localStorage.getItem('_token')}`,
          }
        })
        .then(res=>{
          this.datas=res.data
          console.log(this.datas)
        })
  },
  methods:{
    trySubmitFile(e) {
      for (let i = 0; i < e.target.files.length; i++) {
        this.files_name.push(e.target.files[i])
      }
      var files = Array.from(e.target.files);
      console.log(typeof (files))
      files.forEach((file) => {
        this.myImages.push(file);
        var reader = new FileReader();
        var vm = this;
        reader.onload = function(e) {
          vm.imageUrl.push(e.target.result);
        };
        reader.readAsDataURL(file);
      });
    },
    create(){
      const form = new FormData();

      form.append("name", this.name);
      form.append("period", this.period);
      form.append("save_address", this.address);
      form.append("category_id", this.categorys);
      form.append("file", this.myImages[0]);
      this.$axiosDefault.post('data/create',form,
          {
            headers: {
              'Accept-Language': 'oz',
              'Authorization':  `Bearer  ${localStorage.getItem('_toke')}`,
            }
          })
          .then(()=>{
            this.$toast("Ma'lumot kiritildi", {
              timeout: 2000,
              type:"success"
            });
          })
    }
  }
}
</script>
<style>
.form-card {
  margin-top: 100px;
  width: 100%;
  background-color: #f0f0f0;
  padding: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.form-card h2 {
  text-align: center;
  margin-bottom: 20px;
}

.form-card form {
  margin-bottom: 20px;
}

.form-card label {
  font-weight: bold;
}

.form-card input {
  width: 100%;
  padding: 10px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 4px;
  outline: none;
}

.form-card button {
  padding: 10px 15px;
  background-color: #007bff;
  color: #fff;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  width: 100%;
}

.form-card button:hover {
  background-color: #0056b3;
}
</style>
